.pagination {
    display: flex;
    gap: 10px;
}

.button {
    color: #299DED;
    cursor: pointer;
    border-radius: 50px;
    background: #303539;
    font-size: 25px;
    font-weight: 400;
    padding: 5px 25px;
}

.button.disable {
    pointer-events: none;
    opacity: 0.4;
}

.button.currentPage {
    color: #606365;
}